.content__posts{
    background-color: #fff;
    border-radius: 10px;
    padding: 15px;
}

@media (max-width: 860px) {
    .content__posts {
        border-radius: 0px;
        border-bottom-left-radius: 10px;
        border-bottom-right-radius: 10px;
    }
}
@media (max-width: 600px) {
    .content__posts {
        border-radius: 0px;
    }
}